  src: url(https://fonts.gstatic.com/s/bebasneue/v9/JTUSjIg69CK48gW7PXooxW4.ttf) format('truetype');
}
@font-face {
  font-family: 'Lobster';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/lobster/v28/neILzCirqoswsqX9_oU.ttf) format('truetype');
}
.anecdotes-wrapper {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}
.anecdotes-wrapper .anecdote {
  font-family: 'Lobster', cursive;
  font-size: 2rem;
}
h1 {
  font-size: 3rem;
}
body,
html {
  font-family: 'Bebas Neue', cursive;
  background-color: #B4B483;
}
.vote-count {
  font-size: 2rem;
  color: #E94E63;
  margin: 0 10px;
}
.btn-wrapper button {
  display: inline-block;
  padding: 0.5em 3em;
  border: 0.16em solid #FFFFFF;
  margin: 0 0.3em 0.3em 0;
  box-sizing: border-box;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Bebas Neue', cursive;
  font-weight: 400;
  color: #FFFFFF;
  text-align: center;
  transition: all 0.15s;
  background: transparent;
  cursor: pointer;
  font-size: 1rem;
}
.btn-wrapper button:first-child {
  margin-right: 10px;
}
.btn-wrapper button:hover {
  color: #000000;
  background-color: #FFFFFF;
}
