@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lobster&display=swap');

//  Fonts
@font-family__base: 'Lobster', cursive;
@font-family__secondary: 'Bebas Neue', cursive;

// color
@color-mandy: #E94E63;
@color-swamp-green: #B4B483;

.anecdotes-wrapper {
    text-align: center;
    max-width: 1200px;
    margin: 0 auto;

    .anecdote {
        font-family: @font-family__base;
        font-size: 2rem;
    }
}

h1 {
    font-size: 3rem;
}

body, html {
    font-family: @font-family__secondary;
    background-color: @color-swamp-green;
}

.vote-count {
    font-size: 2rem;
    color: @color-mandy;
    margin: 0 10px;
}

.btn-wrapper {
    button {
        display:inline-block;
        padding:0.5em 3em;
        border:0.16em solid #FFFFFF;
        margin:0 0.3em 0.3em 0;
        box-sizing: border-box;
        text-decoration:none;
        text-transform:uppercase;
        font-family:@font-family__secondary;
        font-weight:400;
        color:#FFFFFF;
        text-align:center;
        transition: all 0.15s;
        background: transparent;
        cursor: pointer;
        font-size: 1rem;
    
    
        &:first-child {
            margin-right: 10px;
        }

        &:hover {
            color:#000000;
            background-color:#FFFFFF;
        }
    }
}